import { useState, useCallback } from '@lynx-js/react';
import { useAuth } from '../../contexts/AuthContext.js';
import { InputField } from '../common/InputField.js';

interface LoginScreenProps {
  onSwitchToSignup: () => void;
}

export function LoginScreen({ onSwitchToSignup }: LoginScreenProps) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const { login } = useAuth();

  const handleLogin = useCallback(async () => {
    'background only';
    if (!email || !password) {
      setError('');
      return;
    }

    setLoading(true);
    setError('');

    try {
      await login(email, password);
    } catch (error: any) {
      setError('');
    } finally {
      setLoading(false);
    }
  }, [email, password, login]);

  const handleEmailInput = useCallback((value: string) => {
    'background only';
    setEmail(value);
  }, []);

  const handlePasswordInput = useCallback((value: string) => {
    'background only';
    setPassword(value);
  }, []);

  return (
    <view className="auth-screen">
      <view className="auth-container">
        <view className="auth-header">
          <text className="auth-title"></text>
          <text className="auth-subtitle"></text>
        </view>

        <view className="auth-form">
          <view className="form-group">
            <text className="form-label"></text>
            <InputField
              value={email}
              placeholder=""
              type="email"
              className="auth-input"
              onInput={handleEmailInput}
            />
          </view>

          <view className="form-group">
            <text className="form-label"></text>
            <InputField
              value={password}
              placeholder=""
              type="password"
              className="auth-input"
              onInput={handlePasswordInput}
            />
          </view>

          {error && (
            <view className="error-message">
              <text className="error-text">{error}</text>
            </view>
          )}

          <view 
            className={`auth-button ${loading ? 'auth-button-disabled' : ''}`}
            bindtap={loading ? undefined : handleLogin}
          >
            <text className="auth-button-text">
              {loading ? '...' : ''}
            </text>
          </view>

          <view className="auth-link-container">
            <text className="auth-link-text"></text>
            <text className="auth-link" bindtap={onSwitchToSignup}>
              
            </text>
          </view>
        </view>
      </view>
    </view>
  );
}