import { useState, useCallback } from '@lynx-js/react';
import { useAuth } from '../../contexts/AuthContext.js';
import { InputField } from '../common/InputField.js';

interface SignupScreenProps {
  onSwitchToLogin: () => void;
}

export function SignupScreen({ onSwitchToLogin }: SignupScreenProps) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const { register } = useAuth();

  const handleSignup = useCallback(async () => {
    'background only';
    if (!email || !password || !confirmPassword) {
      setError('');
      return;
    }

    if (password !== confirmPassword) {
      setError('');
      return;
    }

    if (password.length < 6) {
      setError('6');
      return;
    }

    setLoading(true);
    setError('');

    try {
      await register(email, password);
      console.log('Registration successful');
    } catch (error: any) {
      console.error('Registration error:', error);
      let errorMessage = '';
      if (error && error.code) {
        if (error.code === 'auth/email-already-in-use') {
          errorMessage = '';
        } else if (error.code === 'auth/invalid-email') {
          errorMessage = '';
        } else if (error.code === 'auth/weak-password') {
          errorMessage = '';
        }
      }
      setError(errorMessage);
    } finally {
      setLoading(false);
    }
  }, [email, password, confirmPassword, register]);

  const handleEmailInput = useCallback((value: string) => {
    'background only';
    setEmail(value);
  }, []);

  const handlePasswordInput = useCallback((value: string) => {
    'background only';
    setPassword(value);
  }, []);

  const handleConfirmPasswordInput = useCallback((value: string) => {
    'background only';
    setConfirmPassword(value);
  }, []);

  return (
    <view className="auth-screen">
      <view className="auth-container">
        <view className="auth-header">
          <text className="auth-title"></text>
          <text className="auth-subtitle"></text>
        </view>

        <view className="auth-form">
          <view className="form-group">
            <text className="form-label"></text>
            <InputField
              value={email}
              placeholder=""
              type="email"
              className="auth-input"
              onInput={handleEmailInput}
            />
          </view>

          <view className="form-group">
            <text className="form-label"></text>
            <InputField
              value={password}
              placeholder="6"
              type="password"
              className="auth-input"
              onInput={handlePasswordInput}
            />
          </view>

          <view className="form-group">
            <text className="form-label"></text>
            <InputField
              value={confirmPassword}
              placeholder=""
              type="password"
              className="auth-input"
              onInput={handleConfirmPasswordInput}
            />
          </view>

          {error && (
            <view className="error-message">
              <text className="error-text">{error}</text>
            </view>
          )}

          <view 
            className={`auth-button ${loading ? 'auth-button-disabled' : ''}`}
            bindtap={loading ? undefined : handleSignup}
          >
            <text className="auth-button-text">
              {loading ? '...' : ''}
            </text>
          </view>

          <view className="auth-link-container">
            <text className="auth-link-text"></text>
            <text className="auth-link" bindtap={onSwitchToLogin}>
              
            </text>
          </view>
        </view>
      </view>
    </view>
  );
}